
import { searchApi } from "./conf/api";
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: "search",

components: { XInput, Group, Scroller, LoadMore },
data() {
return {
keyword: "",
searchHeight: "",
scrollerHeight: "",
showSearchContent: false, // 是否显示搜索列表
hotList: [], // 热门搜索数据集合
historyList: [], // 历史搜索数据集合
pageIndex: 1, // 加载数据的页数
commodityList: [], // 商品列表
loadingShow: false, // 为true显示底部加载布局,为false不显示
loadingEnd: true 
};
},
mounted() {
this.init();
},
watch: {
keyword(newVal, oldVal) {
if (newVal.length == 0) {
this.showSearchContent = false;
}
console.log(oldVal, newVal);
}
},

methods: {

init() {
this.searchHeight = window.innerHeight - 102;
this.scrollerHeight = window.innerHeight - 52 + "px";

this.requestSearchApi();

var searchList = JSON.parse(localStorage.getItem("searchList"));

if (searchList != null && searchList.length > 0) {
this.historyList = searchList;
}
},

hotOrhistoryItemClick(item) {
this.keyword = item;
this.searchClick();
},

searchClick() {
if (this.keyword.length > 0) {

if (this.historyList.indexOf(this.keyword) < 0) {
this.historyList.push(this.keyword);
localStorage.setItem("searchList", JSON.stringify(this.historyList));
}
this.showSearchContent = true;

this.pageIndex = 1;

this.requestUGetGoodsH5();
} else {
this.$vux.toast.text("请输入搜索内容", "middle");
}
},

clearClick() {
localStorage.setItem("searchList", JSON.stringify([]));
this.historyList = JSON.parse(localStorage.getItem("searchList"));
},

onCommodityItemClick(item) {
this.$router.push({
name: "commoditydec",
params: {
item: item
}
});
},

onScrolldown() {
this.pageIndex = 1;
this.requestUGetGoodsH5();
},

onScrollup() {
this.pageIndex = this.pageIndex + 1;
this.requestUGetGoodsH5();
},

requestSearchApi() {
this.$request.loading(this, searchApi.GetHotKeys, "get", null, "").then(
success => {
if (success.data.code == 0) {
this.hotList = success.data.data;
}
},
fial => {}
);
},

requestUGetGoodsH5() {
this.$request
.loading(
this,
searchApi.UGetGoodsH5,
"post",
{
PromoterId: "",
CategoryId: "",
Title: this.keyword,
PageSize: 20,
PageIndex: this.pageIndex,
platform: 2,
IsTmall: false,
HasCoupon: true,
NeedFreeShipment: false,
StartPrice: 0
},
""
)
.then(
success => {
this.loadingShow = true;
this.loadingEnd = success.data.code == 0 ? true : false;
if (success.data.code == 0) {
if (this.pageIndex == 1) {

this.commodityList = [];
}

this.commodityList = this.commodityList.concat(success.data.data);
if(this.commodityList.length<8){
this.loadingEnd=false;
}
}
if (this.$refs.scroller != null) {

if (this.pageIndex == 1) this.$refs.scroller.reset({ top: 0 });

this.$refs.scroller.donePullup();
this.$refs.scroller.donePulldown();
}
},
fial => {
if (this.$refs.scroller != null) {

this.$refs.scroller.donePullup();
this.$refs.scroller.donePulldown();
}
}
);
}
},
beforeRouteEnter(to, from, next) {
next(vm => {
if (from.name == "index") {
vm.keyword = "";
vm.showSearchContent = false;
vm.commodityList = [];
}
});
}
};



